import React from 'react';
import { cssDecorator } from '@front-toolkit/utils';

import { Text } from '../typography';

import { CountProps } from './types';

import styles from './Counter.scss';

const cd = cssDecorator(styles);

function Counter(props: CountProps): JSX.Element | null {
  const {
    show,
    disabled,
    size = 'default',
    textLength = 0,
    maxLength = 0
  } = props;

  if (!show) {
    return null;
  }

  return (
    <div className={ cd(
      'counter',
      {
        [`${ size }`]: true,
        disabled
      }
    ) }
    >
      <Text color={ disabled ? 'disabled' : 'secondary' } size="extra_small">
        { textLength }
        { Boolean(maxLength && maxLength > 0) && ` / ${ maxLength }` }
      </Text>
    </div>
  );
}

export default Counter;
